.contact__container {
    grid-template-columns: repeat(2, max-content);
    /* grid-template-columns: 4fr 8fr; */
    justify-content: center;
    column-gap: 5rem;
    padding-bottom: 3rem;
}

.contact__description {
    font-size: var(--small-font-size);
    line-height: 1.6;
    max-width: 360px;
    margin-bottom: var(--mb-1-5);
}

.contact__title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: 3px;
    color: #8ca3c5;
}

.contact__info {
    display: grid;
    row-gap: 1rem;
    grid-template-columns: 300px;

}

.contact__card {
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1rem;
    border-radius: 0.75rem;
    text-align: center;
}

.contact__card-icon {
    font-size: 2rem;
    color: black;
    margin-bottom: var(--mb-0-25);
}

.contact__card-data {
    margin-bottom: var(--mb-0-75);
    display: block;
}

.contact__card-title {
    font-weight: var(--font-medium);
}

.contact__card-title,
.contact__card-data {
    font-size: var(--small-font-size);
    color: rgb(37, 37, 37);
}

.contact__button {
    color: #0D1B2A;
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}

.contact__button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.contact__button:hover .contact__button-icon {
    transform: translate(0.25rem);
}

.contact__form {
    width: 360px;
}

.contact__form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact__form-input {
    position: absolute;

    top: 0;

    left: 0;

    width: 100%;

    height: 100%;

    border: 2px solid #778DA9;

    background: none;

    color: var(--text-color);

    outline: none;

    border-radius: 0.75rem;

    padding: 1.5rem;

    z-index: 1;
}

.contact__form-tag {
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;

}

.contact__form-area {
    height: 11rem;
}

.contact__form-area textarea {
    resize: none;
}

.buttonc {
    display: inline-block;
    background-color: #0D1B2A;
    color: var(--container-color);
    padding: 1.25rem 2rem;
    border-radius: 1rem;
    font-weight: var(--font-medium);
    border: 2px solid var(--title-color-dark);
}

.buttonc:hover {
    color: var(--title-color-dark);
}

@media screen and (max-width: 992px) {

    .contact__container {
        column-gap: 3rem;
    }
}

/* For medium devices */
@media screen and (max-width: 768px) {

    .contact__container {
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }

    .contact__info {
        justify-content: center;
    }

    .contact__form {
        margin: 0 auto;
    }

}

@media screen and (max-width: 576px) {

    .contact__info {
        grid-template-columns: 1fr;
    }

    .contact__form {
        width: 100%;
    }

}