@import url('https://fonts.googleapis.com/css2?family=Baskervville&family=Poppins:wght@400;500;600&display=swap');

/* variables css */
:root {
  --header-height: 3rem;

  /* color mode HSL(hue,saturation, lightness) */
  --sat: 0%;
  --title-color: #E0E1DD;
  --title-color-dark: #778DA9;
  --text-color: #E0E1DD;
  --body-color: #0D1B2A;
  --container-color: #fff;
  /*========== Font and typography ========*/
  --body-font: "Poppins", sans-serif;

  /*5rem = 8px | 1rem = 16px ...*/

  --big-font-size: 2.96rem;

  --h1-font-size: 2.25rem;

  --h2-font-size: 1.5rem;

  --h3-font-size: 1.25rem;

  --normal-font-size: 1rem;

  --small-font-size: 0.875rem;

  --smaller-font-size: 0.813rem;

  --tiny-font-size: 0.625rem;

  /* font weight */
  --font-normal: 400;


  --font-medium: 500;


  --font-semi-bold: 600;

  /*===== Margenes Bottom =====*/

  /* .5rem = 8px | 1rem = 16px ...*/

  --mb-0-25: 0.25rem;

  --mb-0-5: 0.5rem;

  --mb-0-75: 0.75rem;

  --mb-1: 1rem;

  --mb-1-5: 1.5rem;

  --mb-2: 2rem;

  --mb-2-5: 2.5rem;

  --mb-3: 3rem;

  /*=== Z index ===*/
  --z-tooltip: 10;
  --Z-fixed: 100;
  --z-modal: 1000;
}

/*
Responsive typography
*/

@media screen and (max-width: 992px) {
  :root {
    --big-font-size: 2.11rem;
    --h1-font-size: 1.36rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/* base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body {
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
  text-decoration: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

/* reusable css classes */
.section {
  padding: 6rem 0 2rem;
}

.section__title {
  font-size: var(--h1-font-size);
  color: var(--title-color);
}

.section__subtitle {
  display: block;
  font-size: var(--Small-font-size);
  margin-bottom: 4rem;
}

.section__title,
.section__subtitle {
  text-align: center;
}

/* layout */
.container {
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

.separator {
  border-top: 1px solid #515a5f;
  max-width: 37%;
  margin: 72px auto 0;
}

